.modalBackground {
  position: fixed;
  width: 100%;
  background-color: rgba(102, 102, 102, 0.4);
  top: 0;
  left: 0;
  overflow: auto !important;
  display: grid;
  place-items: center;
  z-index: 100;
  padding: 3rem 0;
  animation: show 500ms linear 0s 1 forwards;
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.modalContainer {
  box-sizing: border-box;
  border: 0px solid #353535;
  border-radius: 5px;
  top: 0;
  padding: 1rem 0;
  max-width: var(--max-screen-width);
}
.modalContainer[modal-size=small] {
  width: min(95%, 20rem);
}
.modalContainer[modal-size=medium] {
  width: min(95%, 30rem);
}
.modalContainer[modal-size=medium2] {
  width: min(95%, 40rem);
}
.modalContainer[modal-size=large] {
  width: 75%;
}
.modalContainer[modal-size=xlarge] {
  width: min(95%, 100% - 6rem);
}
.modalContainer > * {
  padding: 0 1rem;
}

.modalHeaderContainer {
  padding-bottom: 20px;
}

.modalHeader {
  font-size: 1.5rem;
  font-weight: 600;
}

.contentContainer {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.footerContainer {
  display: flex;
  justify-content: space-evenly;
  gap: 15px;
}
