.background, .hidden, .shown {
  display: grid;
  place-items: center;
  position: fixed;
  height: 100%;
  z-index: 9998;
  background-color: rgba(102, 102, 102, 0.4);
  padding: 0;
  margin: 0;
  width: 100%;
}

.shown {
  display: grid;
}

.hidden {
  display: none;
}
