.formField {
  display: block;
  width: 100%;
  padding: 5px 5px;
  line-height: 25px;
  font-size: 14px;
  font-weight: 500;
  font-family: inherit;
  border-radius: 6px;
  -webkit-appearance: none;
  transition: border 0.3s ease;
}
.formField::placeholder {
  color: var(--input-placeholder);
}
.formField:focus {
  outline: none;
  border-color: var(--input-border-focus);
}

.formGroup {
  position: relative;
  display: flex;
  height: 25px;
  margin-bottom: 5px;
  width: 100%;
}
.formGroup > span,
.formGroup .formField {
  white-space: nowrap;
  display: block;
}
.formGroup > span:not(:first-child):not(:last-child),
.formGroup .formField:not(:first-child):not(:last-child) {
  border-radius: 0;
}
.formGroup > span:first-child,
.formGroup .formField:first-child {
  border-radius: 2px 0 0 2px;
}
.formGroup > span:last-child,
.formGroup .formField:last-child {
  border-radius: 0 2px 2px 0;
}
.formGroup > span:not(:first-child),
.formGroup .formField:not(:first-child) {
  margin-left: -1px;
}
.formGroup .formField {
  position: relative;
  z-index: 1;
  margin-top: 0;
  margin-bottom: 0;
}
.formGroup > span {
  text-align: center;
  width: 100px;
  font-size: 10px;
  line-height: 25px;
  font-weight: bold;
  transition: background 0.3s ease, border 0.3s ease, color 0.3s ease;
}
.formGroup:focus-within > span {
  color: var(--group-color-focus);
  background: var(--group-background-focus);
  border-color: var(--group-border-focus);
}
