.footer {
  width: 100%;
  padding: 3rem;
  padding-left: 5rem;
  color: #555555;
  background-color: rgba(238, 238, 238, 0.3137254902);
  display: flex;
  gap: 3rem;
  box-sizing: border-box;
  flex-direction: row;
  margin: 0;
}
@media (max-width: 360px) {
  .footer {
    box-sizing: none;
  }
}
@media (max-width: 1148px) {
  .footer {
    flex-direction: column;
    gap: 1.5rem;
    padding: 2rem;
    padding-left: 5rem;
    padding-right: 5rem;
  }
}
@media (max-width: 360px) {
  .footer {
    flex-direction: column;
    gap: 1.5rem;
    padding: 2rem;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.aboutText {
  font-weight: 500;
}

.footerContainer {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  flex: 1;
  font-size: 0.8rem;
  padding-bottom: 1rem;
}
.footerContainer :first-child {
  font-weight: bold;
}
.footerContainer a {
  color: #0275AB;
  font-weight: 500;
  font-size: 0.75rem;
}

.address {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  font-size: 0.8rem;
  color: #555555;
  font-weight: 600;
  gap: 2px;
  white-space: nowrap;
}
.address p {
  display: inline;
  font-weight: bold;
}
@media (max-width: 1148px) {
  .address {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.iconLink {
  text-decoration: none;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  color: white !important;
  background-color: #0E4C64;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.iconThenText {
  display: flex;
  padding-left: 0.5rem;
  gap: 1rem;
  justify-content: flex-start;
  align-items: center;
  font-weight: 500;
}
.iconThenText :first-child {
  color: #0E4C64;
  font-size: 1rem;
}
.iconThenText {
  color: #0E4C64;
}
