.Divider {
  display: flex;
  flex-direction: row;
}
@media (max-width: 1250px) {
  .Divider {
    flex-direction: column;
  }
}

.leftSide {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 10px;
}

.divider {
  border-left: 3px solid var(--primary-color);
  border-radius: 5px;
  height: auto;
  margin-left: 20px;
  margin-right: 20px;
}
@media (max-width: 1250px) {
  .divider {
    border-bottom: 3px solid var(--primary-color);
    border-left: 0;
    margin: 0;
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

.right {
  display: flex;
  margin-top: 10px;
  flex-wrap: wrap;
  width: 560px;
  height: 270px;
  overflow-y: auto;
  overflow-x: wrap;
}
@media (max-width: 1250px) {
  .right {
    width: auto;
    flex-basis: auto;
    align-content: center;
    height: auto;
    overflow-y: wrap;
  }
}
