.toggleContainer {
  display: flex;
  border-radius: 3px;
  width: calc(12rem + 4px);
  position: relative;
  margin-bottom: 1rem;
}
.toggleContainer > span {
  min-width: 7rem;
  text-align: center;
  font-size: 0.75rem;
  font-weight: 600;
  padding: 5px 0;
  cursor: pointer;
  border: solid 1px lightgray;
  color: lightgray;
  z-index: 1;
  z-index: unset;
}
.toggleContainer > span:first-child {
  border-radius: 3px 0 0 3px;
}
.toggleContainer > span:nth-child(2) {
  border-radius: 0 3px 3px 0;
}
.toggleContainer > span[data-toggled=true] {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
}
.toggleContainer > span > span {
  position: relative;
}
.toggleContainer:has(.marker[data-toggled=true]) > span:first-child {
  z-index: 3;
  z-index: unset;
  border-color: var(--primary-color);
  color: white;
  transition: border-color 0s 500ms, z-index 0s 500ms, color 500ms 0s;
}
.toggleContainer:has(.marker[data-toggled=false]) > span:nth-child(2) {
  z-index: 3;
  z-index: unset;
  border-color: var(--primary-color);
  color: white;
  transition: border-color 0s 500ms, z-index 0s 500ms, color 500ms 0s;
}
.toggleContainer[data-disabled=true] > * {
  display: none;
}
.toggleContainer[data-disabled=true]:has(.marker[data-toggled=true])::after {
  padding-left: 10px;
  content: "For Approval";
}
.toggleContainer[data-disabled=true]:has(.marker[data-toggled=false])::after {
  padding-left: 10px;
  content: "History";
}

.marker {
  position: absolute;
  min-width: calc(7rem + 2px);
  height: 100%;
  background-color: var(--primary-color);
  z-index: 2;
  z-index: unset;
  transition: right 500ms;
}
.marker::after {
  color: white;
  position: absolute;
  width: calc(7rem + 2px);
  height: 100%;
  display: grid;
  place-items: center;
  cursor: pointer;
  font-size: 0.75rem;
  font-weight: 600;
}
.marker[data-toggled=true]::after {
  content: "For Approval";
}
.marker[data-toggled=false]::after {
  content: "History";
}
.marker:is([data-toggled=false], :not([data-toggled])) {
  right: -2rem;
  border-radius: 0 3px 3px 0;
}
.marker[data-toggled=true] {
  right: calc(100% - (7rem + 2px));
  border-radius: 3px 0 0 3px;
}
