.sideFilter, .sideFilterHidden {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  min-width: 25rem;
  width: fit-content;
  padding: 1.5rem;
  box-sizing: border-box;
  background-color: #f1f7fd;
  left: 0rem;
  height: auto;
  min-height: calc(100vh - var(--content-margin-default));
  max-height: 100rem;
  position: relative;
  transition: max-height 0.5s ease-in;
}
@media (max-width: 480px) {
  .sideFilter, .sideFilterHidden {
    min-height: 0;
    width: 95%;
  }
}

@media (max-width: 480px) {
  .sideFilterHidden {
    max-height: 1.5rem;
    overflow: hidden;
    transition: max-height 0.5s ease-out;
  }
}

.filterHeaderArea {
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
  font-weight: 600;
  color: #353535;
}

.filterFieldContainer, .filterFieldContainerHidden {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.filterFieldContainer > button, .filterFieldContainerHidden > button {
  align-self: center;
}

@media (max-width: 480px) {
  .filterFieldContainerHidden {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }
}

.hideButton {
  color: #052a38;
  display: flex;
  align-items: center;
  width: 100%;
}
.hideButton > div {
  font-size: 1.3rem;
  font-weight: 600;
  color: #353535;
}
.hideButton > .line {
  flex: 1.5;
  margin-left: 1rem;
  margin-right: 1rem;
  display: none;
}
@media (max-width: 480px) {
  .hideButton > .line {
    display: block;
  }
}
.hideButton > .HiddenOnBigScreen {
  display: none;
}
@media (max-width: 480px) {
  .hideButton > .HiddenOnBigScreen {
    display: block;
  }
}
@media (max-width: 480px) {
  .hideButton {
    cursor: pointer;
  }
  .hideButton > div {
    font-size: 1rem;
  }
}

.customButton {
  min-width: 5.5rem;
  padding: 0.6rem;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  color: white;
  font-weight: bold;
  background-color: var(--light-primary-color);
  border: none;
  align-items: center;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  align-self: center;
}
@media (max-width: 480px) {
  .customButton {
    width: 100%;
  }
}

.buttonGroup {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 2rem;
  margin-top: 0.5rem;
}
