.fieldset {
  margin: 0;
  padding: 0;
  overflow: hidden;
  flex: unset !important;
}
.fieldset > legend {
  font-size: 0.75rem;
  margin: 0 10px;
  padding: 0;
  background-color: transparent;
}
.fieldset > input {
  border: none;
  outline: none;
  width: calc(100% - 20px);
  padding: 5px 10px 7.5px 10px;
  font-size: 1rem;
  background-color: transparent;
}
.fieldset > input::placeholder {
  color: transparent;
}
.fieldset > textarea {
  border: none;
  outline: none;
  width: calc(100% - 20px);
  padding: 5px 10px 7.5px 10px;
  font-size: 1rem;
  resize: none;
  background-color: transparent;
}
.fieldset > textarea::placeholder {
  color: transparent;
}
.fieldset:has(textarea:disabled) {
  background-color: transparent;
  border: none;
}
.fieldset:has(textarea:disabled) legend::after {
  content: ":";
  padding-left: 1px;
}
.fieldset:has(input:disabled) {
  border: none;
}
.fieldset:has(input:disabled) legend::after {
  content: ":";
  padding-left: 1px;
}
.longLegend {
  font-size: 0.75rem;
  margin: 0 0px !important;
  padding: 0;
  background-color: transparent;
}

.fieldset:is([input-type=checkbox]) {
  position: relative;
  border: none;
  padding: 5px 0;
}
.fieldset:is([input-type=checkbox])[data-disabled=true] > legend::after {
  content: ":";
  padding-left: 1px;
}

.toggleContainer {
  display: flex;
  border-radius: 3px;
  width: calc(10rem + 4px);
  position: relative;
}
.toggleContainer > span {
  min-width: 5rem;
  text-align: center;
  font-size: 0.75rem;
  font-weight: 600;
  padding: 5px 0;
  cursor: pointer;
  border: solid 1px lightgray;
  color: lightgray;
  z-index: 1;
  z-index: unset;
}
.toggleContainer > span:first-child {
  border-radius: 3px 0 0 3px;
}
.toggleContainer > span:nth-child(2) {
  border-radius: 0 3px 3px 0;
}
.toggleContainer > span[data-toggled=true] {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
}
.toggleContainer > span > span {
  position: relative;
}
.toggleContainer:has(.marker[data-toggled=true]) > span:first-child {
  z-index: 3;
  z-index: unset;
  border-color: var(--primary-color);
  color: white;
  transition: border-color 0s 500ms, z-index 0s 500ms, color 500ms 0s;
}
.toggleContainer:has(.marker[data-toggled=false]) > span:nth-child(2) {
  z-index: 3;
  z-index: unset;
  border-color: var(--primary-color);
  color: white;
  transition: border-color 0s 500ms, z-index 0s 500ms, color 500ms 0s;
}
.toggleContainer[data-disabled=true] {
  /*&:has(.marker[data-toggled="true"])::after {
    padding-left: 10px;
    content: "YES";
  }

  &:has(.marker[data-toggled="false"])::after {
    padding-left: 10px;
    content: "NO";
  }*/
}
.toggleContainer[data-disabled=true] > :not(.disabledLabel) {
  display: none;
}

.disabledLabel {
  padding-left: 10px;
}

.marker {
  position: absolute;
  min-width: calc(5rem + 2px);
  height: 100%;
  background-color: var(--primary-color);
  z-index: 2;
  z-index: unset;
  transition: right 500ms;
  /*&::after {
    color: white;
    position: absolute;
    width: calc(5rem + 2px);
    height: 100%;
    display: grid;
    place-items: center;
    cursor: pointer;
    font-size: 0.75rem;
    font-weight: 600;
  }*/
  /*&[data-toggled="true"]::after {
    content: "YES";
  }

  &[data-toggled="false"]::after {
    content: "NO";
  }*/
}
.marker > span {
  color: white;
  position: absolute;
  width: calc(5rem + 2px);
  height: 100%;
  display: grid;
  place-items: center;
  cursor: pointer;
  font-size: 0.75rem;
  font-weight: 600;
}
.marker:is([data-toggled=false], :not([data-toggled])) {
  right: 0;
  border-radius: 0 3px 3px 0;
}
.marker:is([data-toggled=true]) {
  right: calc(100% - (5rem + 2px));
  border-radius: 3px 0 0 3px;
}

.fieldsetCustom {
  border: solid thin lightgray;
  border-radius: 3px;
  margin: 0;
  padding: 0;
  overflow: hidden;
  flex: unset !important;
}
.fieldsetCustom > legend {
  font-size: 0.75rem;
  color: var(--lightgray);
  margin: 0 10px;
  padding: 0;
  background-color: transparent;
}
.fieldsetCustom > input {
  border: none;
  outline: none;
  width: calc(100% - 20px);
  padding: 5px 10px 7.5px 10px;
  font-size: 1rem;
  background-color: #e9eff6 !important;
}
.fieldsetCustom > input::placeholder {
  color: transparent;
}
.fieldsetCustom > input:disabled {
  background-color: #e9eff6 !important;
}
.fieldsetCustom > input:disabled::-webkit-input-placeholder {
  color: transparent;
}
.fieldsetCustom:has(input:disabled) {
  background-color: #e9eff6 !important;
}
.fieldsetCustom:has(input:disabled) legend::after {
  content: ":";
  padding-left: 1px;
}
